body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
}
.header{
padding: 20px 100px;
background-color: black;
color: white;
display: flex;
justify-content: space-between;
}
.cart-icon .counter{
  right:7px;
  top:-6px;
  position: relative;
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;

}
.cart-icon{
  display: flex; 
}
.icon{
  
  font-size: 25px;
}
.cart-box{
 display: inline-flex;
 flex-direction: row;
 gap:20px;
 /* margin:30px;  */
 box-shadow: 0px 0px 7px black;
 width: 350px;
 border-radius: 5px;
 
} 
main{
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 80vh;
  align-content: center;
  gap: 50px;
}
.img-wrapper{
  /* margin: 30px; */
  padding: 10px;
  width: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid rgb(212, 205, 205);
}
img{
  height: 200px;
}
.content{
  
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex-grow: 1;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  justify-content:center ;
  border-radius: 3px;
  border: 1px solid grey;
}
.quantity div{
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  
}
.quantity button{
  background-color: crimson;
  height: 25px;
  width:25px;

}
.quantity > div:first-child, .quantity > div:last-child{
  background-color: rgb(206, 30, 30);

}
.total{
  position: fixed;
  background-color:rgb(182, 175, 175);
  color: black;
  padding: 17px;
  padding-left: 91vw;
  bottom: 0;
  width: 100%;
  
  
}






code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
